"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { Card, CardContent } from "@/components/ui/card";
import { Play } from "lucide-react";
import Image from "next/image";

// --- SUB-COMPONENTE: CARD DE VÍDEO INDIVIDUAL ---
// Isolamos a lógica de "está tocando ou não" para cada vídeo.
interface VideoCardProps {
  video: {
    id: number;
    youtube_id: string;
    title: string | null;
  };
}

function VideoCard({ video }: VideoCardProps) {
  const [isPlaying, setIsPlaying] = useState(false);
  const thumbnailUrl = `https://img.youtube.com/vi/${video.youtube_id}/maxresdefault.jpg`;

  // Se estiver tocando, renderiza o Iframe do YouTube
  if (isPlaying) {
    return (
      <Card className="overflow-hidden border-none shadow-lg h-full bg-black">
        {/* Aspect Ratio 16/9 para o player */}
        <div className="relative w-full pt-[56.25%]">
          <iframe
            className="absolute top-0 left-0 w-full h-full rounded-lg"
            src={`https://www.youtube.com/embed/${video.youtube_id}?autoplay=1&rel=0&modestbranding=1`}
            title={video.title || "YouTube Video"}
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          />
        </div>
        {video.title && (
          <div className="p-3 bg-white">
            <h3 className="font-medium text-[#0e2432] truncate">
              {video.title}
            </h3>
          </div>
        )}
      </Card>
    );
  }

  // Se NÃO estiver tocando, renderiza a Thumbnail com botão de Play
  return (
    <Card
      className="group cursor-pointer overflow-hidden border-none shadow-md hover:shadow-xl transition-all duration-300 h-full"
      onClick={() => setIsPlaying(true)}
    >
      <CardContent className="p-0 relative">
        {/* Container da Imagem com Aspect Ratio 16/9 */}
        <div className="relative w-full pt-[56.25%] bg-gray-100">
          <Image
            src={thumbnailUrl}
            alt={video.title || "Vídeo thumbnail"}
            fill
            className="object-cover transition-transform duration-500 group-hover:scale-105"
            // Fallback para uma thumb de menor qualidade se a maxres falhar
            onError={(e) => {
              const target = e.target as HTMLImageElement;
              target.src = `https://img.youtube.com/vi/${video.youtube_id}/mqdefault.jpg`;
            }}
          />
          {/* Overlay Escuro e Ícone de Play */}
          <div className="absolute inset-0 bg-black/30 group-hover:bg-black/40 transition-colors flex items-center justify-center">
            <div className="w-16 h-16 bg-white/90 rounded-full flex items-center justify-center group-hover:scale-110 transition-transform duration-300 pl-1 shadow-lg">
              <Play className="w-8 h-8 text-teal-600 fill-teal-600" />
            </div>
          </div>
        </div>

        {/* Título abaixo do vídeo */}
        <div className="p-4 bg-white">
          <h3 className="font-semibold text-lg text-[#0e2432] line-clamp-2 leading-tight group-hover:text-teal-700 transition-colors">
            {video.title || "Assista ao vídeo"}
          </h3>
        </div>
      </CardContent>
    </Card>
  );
}

// --- COMPONENTE PRINCIPAL DA SEÇÃO ---
export default function VideosSection() {
  const [videos, setVideos] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchVideos();
  }, []);

  const fetchVideos = async () => {
    try {
      const { data, error } = await supabase
        .from("videos")
        .select("*")
        .order("created_at", { ascending: false });

      if (error) throw error;
      if (data) setVideos(data);
    } catch (error) {
      console.error("Erro ao buscar vídeos:", error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="py-20 text-center text-gray-500">
        Carregando conteúdos...
      </div>
    );
  }

  if (videos.length === 0) {
    return null; // Não mostra a seção se não houver vídeos
  }

  return (
    <section className="py-20 bg-gray-50" id="conteudos">
      <div className="container mx-auto px-4 max-w-6xl">
        {/* Cabeçalho da Seção melhorado */}
        <div className="text-center mb-12 space-y-2">
          <h2 className="text-3xl md:text-4xl font-bold text-[#0e2432]">
            Conteúdos Educativos
          </h2>
          <p className="text-gray-600 max-w-2xl mx-auto">
            Vídeos e dicas rápidas sobre neurocirurgia e saúde da coluna. Clique
            para assistir.
          </p>
          <div className="w-20 h-1.5 bg-teal-500 mx-auto rounded-full mt-4"></div>
        </div>

        {/* Grid de Vídeos */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 md:gap-8">
          {videos.map((video) => (
            <VideoCard key={video.id} video={video} />
          ))}
        </div>
      </div>
    </section>
  );
}
